*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size:62.5%;
}

body{
  background: #181818;
}
div{
  width:50em;
  height:50em;
  padding:2em;
  margin:0 auto;
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background:linear-gradient(red, blue);
  border-radius:1em;
}
div h1{
    margin-bottom: 1.3em;
  color:aqua;
  font-size:2.5rem;
  text-transform: uppercase;
}

div h1 span{
  color: #181818;
  text-transform: capitalize;
  
  
}

div select{
  background:#181818;
  color: #fff;
  width: 8em;
  height: 2em;
  font-size:2rem;
}
